.Cards {
    padding-top: 16px;
    display: grid;
}

@media (min-width: 700px) {
    .Cards {
        grid-template-columns: 1fr 1fr;
        gap: 20px;
        padding-bottom: 80px;
    }
}

@media (max-width: 700px) {
    .Cards {
        gap: 16px;
        padding-bottom: 56px;
    }
}